.cart-parent {
    width: 100%;
}

.cart-parent thead tr th {
    padding: 3px 5px;
}

.cart-parent tbody tr:first-of-type {
    margin-top: 30px;
}

.cart-parent tbody tr td {
    text-align: center;
}

.cart-parent tbody tr td.table-cart-price {
    font-weight: 700;
}

.cart-parent tbody tr td span {
    border: 2px solid black;
    display: inline-block;
    width: 20px;
    height: 23px;
    line-height: 20px;
    cursor: pointer;
    margin-left: 7px;
    margin-right: 7px;
}

@media (max-width: 740px) {
    .cart-parent tbody tr td span {
        margin: 0;
    }
}

.cart-parent tbody tr td span:hover {
    background-color: black;
    color: white;
}

.cart-parent tbody tr td div img {
    width: 60px;
    height: 60px;
}

.cart-parent tbody tr td div.trash-image {
    cursor: pointer;
}

.cart-parent tbody tr td div.trash-image img {
    width: 30px;
    height: 30px;
}
.clear_and_order {
    margin-top: 30px;
    text-align: right;
    margin-right: 20px;
}

.clear_and_order .clear_cart {
    padding: 8px 20px;
    background-color: transparent;
    color: red;
    border: 1px solid red;
    cursor: pointer;
    border-radius: 9px;
    outline: none;
}

.clear_and_order .clear_cart:hover {
    background-color: red;
    color: white;
}

.clear_and_order .order_total {
    margin-top: 10px;
}

.clear_and_order .order_total p {
    margin-top: 7px;
    font-family: 'Just Another Hand', cursive;
    font-size: 35px;
}

.clear_and_order .order_total p span {
    font-weight: bolder;
}

.clear_and_order .order_stripe {
    margin-top: 13px;
}

.spinner-parent .Loader {
    margin-top: 0;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: fixed;
    left: 50%;
    top: 50%;
}
